import React from 'react'
import { QueryRenderer } from 'Components'
import getProduct from './Requests/getProduct'
import ViewProduct from './ViewProduct'

function Details(props) {
  return <div>
    <QueryRenderer query={getProduct} variables={{id: props.match.params.id}} component={data => <ViewProduct product={data.getSpecificProduct}/>}/>
  </div>
}

export default Details