import React from 'react'
import FormControl from '@material-ui/core/FormControl'
import Button from '@material-ui/core/Button'
import FormLabel from '@material-ui/core/FormLabel'
import { makeStyles } from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  formRow: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    paddingBottom: theme.spacing(1),
    alignItems: 'stretch'
  },
  label: {
    paddingLeft: theme.spacing(1),
    border: '1px solid #efefef',
    flex: 1,
    alignItems:'center',
    display: 'flex'
  }
}))

function UploadComponent(props){
  const classes = styles()
  return(
    <>
    <FormLabel>{props.label}</FormLabel>
    <FormControl fullWidth className={classes.formRow+" "+classes.container}>
      <label className={classes.label} htmlFor={`#${props.name}`}>{props.file || "No file selected"}</label>
      <Button onClick={props.onButtonClick} variant="contained" color="secondary" className={classes.half}> Select File </Button>
      <input ref={props.inputRef} type="file" name={props.name}  id={props.name} multiple hidden onChange={props.handleChange}/>
    </FormControl>
    </>
  )
}

function Upload(props){

  let [file, setFile] = React.useState('')

  function buttonClick(){
    document.querySelector(`#${props.name}`).click()
  }

  function handleChange(e){
    if(e.target.files.length === 0){
      return 
    }
    let str = e.target.files[0].name
    //console.log(e.target.files.length)
    for(let i = 1; i < e.target.files.length; i++){
      str+= ", "+e.target.files[i].name
      //console.log(str)
    }
    //console.log(str)
    setFile(str)
    
  }
  //console.log(file)

  return (
    <UploadComponent 
      onButtonClick={buttonClick}
      handleChange={handleChange}
      file={file}
      {...props}
      />
  )
}

export default Upload