import React from 'react'
import {makeStyles} from '@material-ui/core/styles'
import PropTypes from 'prop-types'
import {TablePagination, Divider, Grid} from '@material-ui/core'
import {Loading} from 'Components'
import {snackbarError} from 'Store/actions/snackbar'
import ErrorUtils from 'Services/ErrorUtils'

const styles = makeStyles(theme => ({
  loadingContainer: {
    position: "relative",
    overflow: "hidden",
    margin: 0,
    width: "100%",
  },
  loader: {
    position: "absolute",
    width: "100%",
    height: "100%",
    zIndex: 1,
    background: "rgba(0, 0, 0, .3)",
    top: 0,
    left: 0
  }
}))

function Paginated({result, ...props}) {
  const [limit, setLimit] = React.useState(15)
  let [fetching, setFetching] = React.useState(false)
  const classes = styles()

  function refetch(page, limit) {
    setFetching(true)
    // console.log(fetching)
    props.relay.refetch({page, limit}, null, err => {
      setFetching(false)
      if(err) {
        console.error(err)
        ErrorUtils(err)
      }
    })
  }
  return <>
    <Grid className={classes.loadingContainer+" "+props.resultContainerClass} container={props.container} spacing={props.spacing}>
      {
        result.docs.map(item => props.render(item))
      }
      {fetching && <div className={classes.loader}><Loading isLoading={true}/></div>}
    </Grid>
    <Divider />
    <TablePagination
      rowsPerPageOptions={[15, 30, 50]}
      component="div"
      count={result.totalDocs}
      rowsPerPage={limit}
      page={result.page - 1}
      onChangePage={(e, page) => refetch(page + 1, limit)}
      onChangeRowsPerPage={e => { setLimit(e.target.value); refetch(result.page, e.target.value)}}
      />
  </>
}

Paginated.defaultProps = {
  render: () => {},
  container: false,
  spacing: 0
}

Paginated.propTypes = {
  refetch: PropTypes.func,
  result: PropTypes.object.isRequired,
  render: PropTypes.func.isRequired,
  container: PropTypes.bool,
  spacing: PropTypes.number
}

export default Paginated