import React from 'react'
import {categoryQuery} from './AddCategoryForm'
import { QueryRenderer} from 'Components'
import { snackbarInfo } from 'Store/actions/snackbar'
import CategoryComp from './Category'

function Category(props) {

  function onSubmit(data) {
    console.log(data)
    if(data.type === "CHILD" && !data.child) {
      snackbarInfo("If child is selected please select a parent")
    }
  }
  return <div>
    <QueryRenderer query={categoryQuery} component={(data) => <CategoryComp category={data.getCategory.category}/>}/>
  </div>
}

export default Category