import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'
import {Grid, Avatar, Typography} from '@material-ui/core'
import {makeStyles} from '@material-ui/core/styles'
import CustomList from 'Components/CustomList'
import Tabs from 'Components/Tabs'
import ViewTournaments from './Comps/ViewTournaments'
import ViewTransaction from './Comps/ViewTransaction'
import ViewWithdraw from './Comps/ViewWithdraw'

const query = graphql`
  query MoreDetailsUserQuery ($data: GetUserInput!){
    getUserQuery(data: $data) {
      user {
        username
      }
    }
  }
`

const styles = makeStyles(theme => ({
  root: {
    width: "100%",
    margin: 0
  },
  avatar: {
    width: theme.spacing(7),
    height: theme.spacing(7),
    color: "#fff",
    backgroundColor: theme.palette.secondary.main,
    fontSize: theme.spacing(3)
  },
  addMargin: {
    '&> *':{
      marginBottom: theme.spacing(1)
    }
  }
}))

function MoreDetails(props) {
  return <QueryRenderer query={query} variables={{data: {username: props.match.params.username}}} component={data => <MoreDetailsComponent {...data} />}/>
}

function getOverview(user = {}) {
  // console.log
  return [{
      id: 1,
      primaryText: "Wallet",
      secondaryText: `Rs. ${user.walletBalance}`
    },
    {
      id: 2,
      primaryText: "Bonus",
      secondaryText: `Rs. ${user.bonus}`
    },
    {
      id: 3,
      primaryText: "Games Played",
      secondaryText: user.registeredTillToday
    },
    {
      id: 4,
      primaryText: "Phone",
      secondaryText: user.phone
    }
  ]
}

function MoreDetailsComponent({getUserQuery}) {
  console.log(getUserQuery)
  const classes = styles()

  if(!getUserQuery.user){
    return <div>User not found</div>
  }

  const user = getUserQuery.user
  const overview = getOverview(user)
  // console.log(user.registeredTournaments)
  return <Grid container spacing={1} className={classes.root}>
  </Grid>
}

export default MoreDetails;