import * as yup from 'yup'
import graphql from 'babel-plugin-relay/macro'

export const brandFormSchema = [
  {
    type: "text",
    name: "label",
    required: true,
    label: "Brand Name"
  },
]

export const brandValidationSchema = yup.object().shape({
  label: yup.string().required()
})

export const brandMutation = graphql`
mutation AddBrandFormMutation($data: AddBrandInput!){
  addBrand(data: $data) {
    brand {
      label
    }
  }
}
`

export const brandQuery = graphql`
query AddBrandFormQuery{
  getBrand {
      label
      categories
      products
      id
  }
}
`