import React from 'react'
import ProductForm from './ProductForm'
import { QueryRenderer } from 'Components'
import { getProductFields } from './AddProductForm'

function AddProduct(props) {
  return <div>
    <QueryRenderer query={getProductFields} variables={{type: "CHILD"}} 
    component={data => <ProductForm 
      category={data.getCategory.category} 
      brand={data.getBrand}
      color={data.getColor.color}/>} />
  </div>
}

export default AddProduct