import React from 'react'
import {ExpansionPanel, IconWithToolTip, CommitRelay, Modal, FormWithRelay} from 'Components'
import Delete from '@material-ui/icons/Delete'
import { Paper, Divider, TextField } from '@material-ui/core'
import {makeStyles} from '@material-ui/core/styles'
import graphql from 'babel-plugin-relay/macro'
import { snackbarSuccess } from 'Store/actions/snackbar'
import Edit from '@material-ui/icons/Edit'
import {formSchema} from './EditCategoryForm'

const deleteMutation = graphql`
mutation DisplayCategoryMutation($id: ID!){
  deleteCategory(id: $id) {
    category {
      id
      label
    }
  }
}
`

const styles = makeStyles(theme => ({
  heading: {
    display: "flex",
    width: "100%",
    alignItems: "center",
    justifyContent: "space-between",
    padding: theme.spacing(1)
  }
}))

function deleteCategory(id) {
  if(!window.confirm("Do you wish to perform")) {
    return
  }
  CommitRelay({id}, deleteMutation, (response, error) => {
    if(!error) {
      snackbarSuccess("Category deleted successfully")
    }
  }, {updater: (store, data) => {
    store.delete(id)
    console.log(data)
  }})
}

function Panel(props) {
  const classes = styles()
  console.log(props)
  if(!props.item) {
    return null
  }
  if(props.item.children?.length > 0) {
    const newChild = props.item.children.map(item => props.all[item])
    // console.log(newChild)
    return <ExpansionPanel heading={props.item.label}>
      {newChild.map((item, index) => <div style={{width: "100%"}}>
        <Panel key={index} all={props.all} item={item} editItem={props.editItem}/>
    </div>)}
    </ExpansionPanel>
    
  }
  return <div className={classes.heading}>{props.item.label} 
  <div>
    <IconWithToolTip title={"Edit Categor"} icon={<Edit />} onClick={() => props.editItem(props.item)}/>
    <IconWithToolTip title={"Delete Category"} icon={<Delete />} onClick={() => deleteCategory(props.item.id)}/>
  </div>
  </div>
}

function DisplayCategory(props) {
  // const tree = makeTree(props.categories)
  let all = {}
  props.categories.forEach(item => all[item.id] = item)
  const filtered = props.categories.filter(item => item.type === "MAIN")
  // console.log(filtered, all)

  function editItem(item) {
    console.log(item)
  }

  return <div>
    <Modal open={false} title={"Edit Category"}>
      <FormWithRelay formSchema={formSchema}/>
    </Modal>
    {
      filtered.map((item, index) => <Panel key={index} item={item} all={all} editItem={editItem}/>)
    }
  </div>
}

export default DisplayCategory