import React from 'react'
import {brandFormSchema, brandMutation, brandQuery, brandValidationSchema} from './AddBrandForm'
import FormWithRelay from 'Components/FormComps/FormWithRelay'
import { CustomList, QueryRenderer, LoadingIcon, CommitRelay } from 'Components'
import { Box } from '@material-ui/core'
import Delete from '@material-ui/icons/Delete'
import deleteBrandMutation from './DeleteBrand'
import Edit from '@material-ui/icons/Edit'
import { snackbarSuccess } from 'Store/actions/snackbar'

function Brand(props) {
  return <div>
    <AddBrand />
    <Box p={1}/>
    <QueryRenderer query={brandQuery} component={data => <BrandDisplay {...data}/>}/>
  </div>
}

function AddBrand(props) {
  return <FormWithRelay 
    formSchema={brandFormSchema} 
    validationSchema={brandValidationSchema} 
    mutation={brandMutation} 
    message={"Brand added successfully"} 
    title={"Add brand"}
    />
}

function BrandDisplay(props) {
  
  let [deleteId, setDeleteId] = React.useState("")

  function deleteBrand(id) {
    // console.log(window.confirm("Do yu wish to delete?"))
    if(!window.confirm("Do you wish to delete?")) {
      return
    }
    // console.log("Called")
    setDeleteId(id)
    CommitRelay({id}, deleteBrandMutation, (response, error) => {
      // console.log(response, error)
      setDeleteId("")
      if(!error) {
        snackbarSuccess(response.deleteBrand.message)
      }
    })
  }

  const items= props.getBrand?.map(item => ({
    ...item,
    primaryText: item.label,
    secondaryIcon: <LoadingIcon isLoading={item.id === deleteId} title={"Delete Brand"} icon={<Delete />} onClick={() => deleteBrand(item.id)}/>
  }))
  return <CustomList items={items} heading={"Brands"}/>
}

export default Brand