import React from 'react'
import {createRefetchContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import {snackbarError} from 'Store/actions/snackbar'
import {Paginated} from 'Components'
import { ProductCard } from 'DumbComps/Product'
import { useHistory } from 'react-router-dom'

function RefreshContainerProduct(props) {
  // console.log(props)
  const result = props.products.getProduct
  const history = useHistory()

  // function refetchPage(page, limit) {
  //   setFetching(true)
  //   props.relay.refetch({page, limit}, null, err => {
  //     setFetching(false)
  //     if(err) {
  //       snackbarError(`Unable to fetch ${err}`)
  //     }
  //   })
  // }

  function onClick(data) {
    history.push(`/product/view/${data.id}`)
  }

  return <Paginated 
    result={result}  
    relay={props.relay} 
    render={data => <ProductCard {...data} key={data.id} productClick={() => onClick(data)}/>}
    container={true}
    spacing={1}/>
}

export default createRefetchContainer(RefreshContainerProduct, {
  products: graphql`
    fragment RefreshContainerProduct_products on Query 
    @argumentDefinitions(
      page: {type: "Int", defaultValue: 1}
      limit: {type: "Int", defaultValue: 15}
    ) {
      getProduct(paginate: {page: $page, limit: $limit}){
        docs {
          label
          price
          discount
          id
          images
          cached {
            brand
            thumbnail
          }
        }
        page
        limit
        totalPages
        totalDocs
      }
    }
  `
}, graphql`
query RefreshContainerProductQuery($page: Int!, $limit: Int!) {
  ...RefreshContainerProduct_products @arguments(page: $page, limit: $limit)
}
`)