import React from 'react'
import {colorFormSchema, colorMutation, colorQuery, colorValidationSchema} from './AddColorForm'
import Form from 'Components/FormComps/FormWithRelay'
import { CustomList, QueryRenderer, IconWithToolTip, Modal, CommitRelay } from 'Components'
import Delete from '@material-ui/icons/Delete'
import Edit from '@material-ui/icons/Edit'
import { Box } from '@material-ui/core'
import deleteMutation from './DeleteMutation'
import { snackbarSuccess } from 'Store/actions/snackbar'

function Color(props) {

  function onSubmit(data) {
    console.log(data)
  }

  return <div>
    <Form formSchema={colorFormSchema} 
      validationSchema={colorValidationSchema} 
      btnText={"Add Color"}
      mutation={colorMutation}
      message={"Color Added successfully"}
      />
    <Box p={1} />
    <QueryRenderer query={colorQuery} component={data => <ColorComp {...data}/>}/>
  </div>
}

function ColorComp(props) {
  let items = []
  const [modal, setModal] = React.useState(false)

  function deleteColor(id) {
    if(!window.confirm("Do you wish to delete")) {
      return
    }
    console.log(id)
    CommitRelay({id}, deleteMutation, (response, error) => {
      if(!error) {
        snackbarSuccess("Color deleted Successfully")
      }
    }, {updater: (store) => {
      // store.delete(id)
    }})
  }

  try {
    // console.log(props.getColor.color)
    items = props.getColor.color.map(item => ({
      primaryText: item.label,
      id: item.id,
      secondaryText: item.colorCode,
      icon: <IconWithToolTip title={"Edit"} icon={<Edit />} />,
      secondaryIcon: <IconWithToolTip title={"Delete"} icon={<Delete />} onClick={() => deleteColor(item.id)}/>
    }))
  }catch(err) {
    console.log(err)
  }
  return <>
    <Modal />
    <CustomList items={items} heading={"Available colors"} divider/>
  </>
}

export default Color