import React from 'react'
import { Typography, TablePagination, TableBody, Table, TextField, InputAdornment, Box, IconButton, Tooltip } from '@material-ui/core'
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import PropTypes from 'prop-types';
import Search from '@material-ui/icons/Search'
import {makeStyles} from '@material-ui/core/styles'
import Close from '@material-ui/icons/Close'
import Download from '@material-ui/icons/GetApp'
import Add from '@material-ui/icons/Add'
import IconWithToolTip from './IconWithToolTip'

const styles = makeStyles(theme => ({
  row: {
    display: "flex",
    flexDirection: "row",
    width: "100%",
    alignItems: "center",
    justifyContent: "space-between",
    paddingTop: theme.spacing(2)
  },
  heading: {
    fontWeight: 300
  },
  container: {
    position: "relative",
    overflow: "hidden",
    borderRadius: 4,
    background: theme.palette.background.paper,
    boxShadow: theme.shadows[2]
  },
  scrollTable: {
    width: "100%",
    position: "relative",
    overflow: "auto"
  },
  searchbar: {
    display: "flex",
    alignItems: "center"
  }
}))

/*
  create a custom implementation of material table it adds a bundle size of 300Kb
*/

function filterData(data = [], search = "") {
  return data.filter(row => {
    for(let key in row) {
      // console.log(key, row)
      if(row[key].toString().toLowerCase().indexOf(search.toLowerCase()) !== -1) {
        return true
      }
    }
    return false
  })
}

export default function(props) {
  // let [rows, setRows] = React.useState(10)
  let [data, setData] = React.useState({page: 0, rows: 10})
  let [sort, setSort] = React.useState({order: props.defaultOrder, orderBy: props.defaultOrderBy})
  let [search, setSearch] = React.useState("")
  const classes = styles()


  function onRequestSort(e, property) {
    const isAsc = sort.orderBy === property && sort.order === 'asc';
    setSort({order: isAsc ? "desc" : "asc", orderBy: property})
  }

  function downloadCSV(csv) {
    var csvFile;
    var downloadLink;

    // CSV file
    csvFile = new Blob([csv], {type: "text/csv"});

    // Download link
    downloadLink = document.createElement("a");

    // File name
    downloadLink.download = "rawData.csv";

    // Create a link to the file
    downloadLink.href = window.URL.createObjectURL(csvFile);

    // Hide download link
    downloadLink.style.display = "none";

    // Add the link to DOM
    document.body.appendChild(downloadLink);

    // Click download link
    downloadLink.click();
  }

  function exportCsv() {
    // var csv = [];
    // var rows = document.querySelectorAll("table tr");
    let headerKeys = props.columns.map(column => ({
      key: column.id,
      value: column.label
    }))
    let csv = props.tableData.map(row => {
      return headerKeys.map(column => row[column.key])
    })
    csv.unshift(headerKeys.map(column => column.value))
    downloadCSV(csv.join("\n"), "filename");
  }


  let a = stableSort(filterData(props.tableData, search), getComparator(sort.order, sort.orderBy))
            .slice(data.page * data.rows, data.page * data.rows + data.rows)
  // console.log(a)

  return <div className={props.className || classes.container}>
    <Box p={1} className={classes.row}>
      <Typography variant="h4" className={classes.heading}>{props.title || "Data"}</Typography>
      <div className={classes.searchbar}>
      <TextField placeholder="search" InputProps={{
        startAdornment: <InputAdornment position="start"><Search /></InputAdornment>,
        endAdornment: <InputAdornment position="end">
          <IconButton disabled={search.length > 0} onClick={e => setSearch("")}> <Close /> </IconButton>
        </InputAdornment>
      }} value={search} onChange={e => setSearch(e.target.value)}/>
      <Tooltip title={"Download data in csv format"}>
        <IconButton onClick={exportCsv}>
          <Download />
        </IconButton>
      </Tooltip>
      <IconWithToolTip title={"Add"} onClick={props.addClick} icon={<Add />}/>
      </div>
    </Box>
    <div className={classes.scrollTable}>
      <Table>
        <EnhancedTableHead headCells={props.columns} {...sort} onRequestSort={onRequestSort} />
        <EnhancedBody heading={props.columns} body={a}/>
      </Table>
    </div>
    <TablePagination 
      component={"div"}
      count={props.tableData.length}
      rowsPerPageOptions={[10, 20, 50]}
      rowsPerPage={data.rows}
      page={data.page}
      onChangeRowsPerPage={e => setData({...data, rows: e.target.value})}
      onChangePage={(e, page) => setData({...data, page})}
    />
  </div>
}


function descendingComparator(a ={}, b ={}, orderBy) {
  if(orderBy === undefined) {
    return 0
  }else if(a[orderBy] === undefined || b[orderBy] === undefined) {
    return 0
  }
  if(typeof a[orderBy] !== "number") {
    return a[orderBy].toString().localeCompare(b[orderBy].toString());  
  }
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}


function EnhancedTableHead(props) {
  const { order, orderBy, onRequestSort } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };
  // console.log(order, orderBy)
  return (
    <TableHead>
      <TableRow>
        {props.headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'left'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              onClick={createSortHandler(headCell.id)}
            >
              {headCell.label}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

EnhancedTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.oneOf(['asc', 'desc']).isRequired,
  orderBy: PropTypes.string.isRequired,
};

function EnhancedBody(props) {
  const {heading, body} = props
  // console.log(heading)
  // function getRow(row){
  //   return 
  // }

  return <TableBody>
    {
      body.map((row, index) => <TableRow key={index}>
        {
          heading.map((column, index) => <TableCell key={index} align={column.numeric ? "right": "left"}>{row[column.id]}</TableCell>)
        }
      </TableRow>)
    }
  </TableBody>
}