import React from 'react'
import {Collapse, Typography} from '@material-ui/core'
import {makeStyles} from '@material-ui/core/styles'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const styles = makeStyles(theme => ({
  heading: {
    padding: theme.spacing(1),
    cursor: "pointer",
    display: "flex",
    width: "100%",
    justifyContent: "space-between",
    alignItems: "center",
    transition: "all .3s linear",
    '&:hover': {
      background: 'rgba(0, 0, 0, .1)'
    }
  },
  content: {
    padding:theme.spacing(1),
    paddingLeft: theme.spacing(2)
  }
}))

function CollapseExpansion(props) {
  const classes = styles()
  let [open,setOpen] = React.useState(false)

  return <div>
    <Typography className={classes.heading} variant="body1" onClick={() => setOpen(!open)}>{props.heading} <ExpandMoreIcon /></Typography>
    <Collapse className={classes.content} in={open}>{props.children}</Collapse>
  </div>
}

export default CollapseExpansion