import React from 'react'
import {CommitRelay, LoadingButton} from 'Components'
import Form from './index'
import PropTypes from 'prop-types'
import {snackbarSuccess} from 'Store/actions/snackbar'

function FormWithRelay({btnText, mutation, ...props}){
  const [requesting, setRequesting] = React.useState(false)

  // console.log(mutation)

  function onSubmit(data) {
    console.log(data)
    setRequesting(true)
    // console.log(data)
    // console.log(mutation)
    CommitRelay({data}, mutation, (response, error) => {
      setRequesting(false)
      if(props.handleResponse) {
        return props.handleResponse(response, error)
      }
      if(!error) {
        snackbarSuccess(props.message)
      }
    })
  }

  return <Form {...props} onSubmit={onSubmit}>
    {props.children}
    <LoadingButton isLoading={requesting} type="submit">{btnText}</LoadingButton>
  </Form>
}

FormWithRelay.defaultProps = {
  btnText: "Submit",
  response: () => {},
  message: "Action Completed!"
}

FormWithRelay.propTypes = {
  mutation: PropTypes.object.isRequired,
  btnText: PropTypes.string,
  handleResponse: PropTypes.bool,
  response: PropTypes.func.isRequired,
  message: PropTypes.string.isRequired
}

export default FormWithRelay