import { Paginated } from 'Components'
import React from 'react'
import { createRefetchContainer } from 'react-relay'
import graphql from 'babel-plugin-relay/macro'

function OrderRefetch(props) {
  return <Paginated 
    result={props.orders.getOrders} 
    relay={props.relay}
    render={item => <div key={item.id}>{item.id}</div>}
    />
}

export default createRefetchContainer(OrderRefetch, {
  orders: graphql`
    fragment OrderRefetch_orders on Query 
    @argumentDefinitions(
      page: {type: "Int!", defaultValue: 1}
      limit: {type: "Int!", defaultValue: 15}
    )
    {
      getOrders(paginate: {page: $page, limit: $limit}) {
        docs {
          id
          amount
          placed
          totalDiscount
        }
        page
        limit
        totalDocs
        totalPages
      }
    }
  `
}, graphql`
  query OrderRefetchQuery($page: Int!, $limit: Int!){
    ...OrderRefetch_orders @arguments(page: $page, limit: $limit)
  }
`)