import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import RefreshContainerProduct from './RefreshContainerProduct'
import {QueryRenderer} from 'Components'

const query = graphql`
query RequestProductQuery {
  ...RefreshContainerProduct_products
}
`

function RequestProduct(props) {
  return <div>
    <QueryRenderer query={query} component={data => <RefreshContainerProduct products={data}/>}/>
  </div>
}

export default RequestProduct

