import * as yup from 'yup'
import graphql from 'babel-plugin-relay/macro'

const required = true

export const productFormSchema = [
  {
    type: "text",
    name: "label",
    label: "Product Name",
    required
  },
  {
    type: "number",
    name: "price",
    label: "Actual Product price",
    helperText: "Price of product with discount",
    required
  },
  {
    type: "number",
    name: "discount",
    label: "Discount",
    required
  },
  {
    type: "text",
    name: "description",
    label: "Description",
    multiline: true,
    rows: 5,
    required
  },
  {
    type: "select",
    name: "gender",
    options: ["Men", "Women", "Boy", "Girl"],
    label: "Gender",
    required: true
  },
  {
    type: "upload",
    name: "poster",
    label: "Main Image",
    helperText: "Main image here. Once product is created you can add more"
  }
]

export const productValidationSchema = yup.object().shape({
  label: yup.string().required(),
  discount: yup.number().max(100).required(),
  price: yup.number().integer().required(),
  description: yup.string().required(),
  brand: yup.string().required(),
  category: yup.string().required(),
  color: yup.string().required(),
  gender: yup.string().required()
})

export const addProductMutation = graphql`
mutation AddProductFormMutation($data: AddProductInput!) {
  addProduct(data: $data) {
    product {
      label
    }
  }
}
`

export const getProductFields = graphql`
query AddProductFormQuery($type: String!){
  getCategory(filter: {type: $type}) {
    category {
      ...ProductForm_category
    }
  }
  getBrand {
    ...ProductForm_brand
  }
  getColor {
    color {
      ...ProductForm_color
    }
  }
}
`