import React from 'react'
import Form from 'Components/FormComps'
import {LoadingButton, CommitRelay} from 'Components'
import {productFormSchema, productValidationSchema, addProductMutation} from './AddProductForm'
import { snackbarInfo, snackbarSuccess } from 'Store/actions/snackbar'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'

function ProductForm(props) {

  const [requesting, setRequesting] = React.useState(false)

  function onSubmit(data) {
    let poster = data.poster
    if(!poster){
      return snackbarInfo("Select a poster image of product")
    }else if(poster[0].name.search(/(\.jpg|\.jpeg|\.png|\.gif|\.webp)$/i) === -1) {
      return snackbarInfo("Only jpg, png and jpeg images are allowed")
    }else if(poster[0].size > (1024  * 1000)) {
      return snackbarInfo("Upload limit of image file is 1MB. Compress and upload image")
    }
    delete data.poster
    setRequesting(true)
    CommitRelay({data}, addProductMutation, (response, error) => {
      setRequesting(false)
      if(!error) {
        snackbarSuccess("Product uploaded successfully")
      }
    }, {uploadables: {poster: poster[0]}})
  }

  // console.log(productValidationSchema)
  const newFields = [{
    name: "brand",
    label: "Brand",
    type: "select",
    options: props.brand.map(item => ({label: item.label, value: item.id})),
    required: true
  },{
    name: "category",
    label: "Category",
    type: "select",
    options: props.category.map(item => ({label: item.label, value: item.id})),
    required: true
  },{
    name: "color",
    label: "Color",
    type: "select",
    options: props.color.map(item => ({label: item.label, value: item.id})),
    required: true
  }]
  return <Form formSchema={productFormSchema.concat(newFields)} onSubmit={onSubmit} validationSchema={productValidationSchema}>
    <LoadingButton type="submit" isLoading={requesting}> Add Product </LoadingButton>
  </Form>
}

export default createFragmentContainer(ProductForm, {
  category: graphql`
    fragment ProductForm_category on Category @relay(plural: true){
      label
      id
    }
  `,
  brand: graphql`
    fragment ProductForm_brand on Brand @relay(plural: true){
      label
      id
    }
  `,
  color: graphql`
    fragment ProductForm_color on Color @relay(plural: true) {
      label
      id
    }
  `
})