import React from 'react'
import { createFragmentContainer } from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import {categoryFormSchema, categoryValidationSchema, categoryMutation} from './AddCategoryForm'
import Form from 'Components/FormComps'
import {LoadingButton, CommitRelay} from 'Components'
import DisplayCategory from './DisplayCategory'
import { snackbarInfo, snackbarSuccess } from 'Store/actions/snackbar'

function Category(props) {

  return <div>
    <DisplayCategory categories={props.category}/>
    <FormComp categories={props.category}/>
  </div>
}

function getItems(data = []) {
  let item = {
    name: "parent",
    label: "Parent Node",
    options: [],
    required: true,
    type: "select"
  }
  for(let i = 0; i < data.length; i++) {
    if(data[i].type !== "CHILD") {
      item.options.push({
        label: data[i].label,
        value: data[i].id
      })
    }
  }
  // console.log(item)
  return item
}

function FormComp(props) {

  const [requesting, setRequesting] = React.useState(false)

  function onSubmit(data) {
    // console.log(data)
    if(data.type === "CHILD" && !data.parent) {
      return snackbarInfo("If child is selected please select a parent")
    }else if(data.type === "MAIN" && data.parent) {
      return snackbarInfo("If it's a main node. Then, don't select a child node")
    }
    let poster = data.poster
    if(!poster){
      return snackbarInfo("Select a poster image of product")
    }else if(poster[0].name.search(/(\.jpg|\.jpeg|\.png|\.gif|\.webp)$/i) === -1) {
      return snackbarInfo("Only jpg, png and jpeg images are allowed")
    }else if(poster[0].size > (1024  * 1000)) {
      return snackbarInfo("Upload limit of image file is 1MB. Compress and upload image")
    }
    delete data.poster
    setRequesting(true)
    CommitRelay({data}, categoryMutation, (response, error) => {
      setRequesting(false)
      if(!error) {
        snackbarSuccess(response.addCategory.message)
      }
    }, {uploadables: {poster: poster[0]}})
  }

  // const combinedSchema = [...categoryFormSchema, getItems(props.categories)]

  return <Form formSchema={[...categoryFormSchema, getItems(props.categories)]} validationSchema={categoryValidationSchema} onSubmit={onSubmit}>
  <LoadingButton type="submit" isLoading={requesting}>Submit</LoadingButton>
</Form>
}

export default createFragmentContainer(Category, {
  category: graphql`
    fragment Category_category on Category @relay(plural:true){
      label
      type
      parent
      id
      children
    }
  `
})