import React from 'react'
import {Card, Typography, CardMedia, CardActionArea, Grid} from '@material-ui/core'
import PropTypes from 'prop-types'
import {makeStyles} from '@material-ui/core/styles'
import Bookmark from '@material-ui/icons/Bookmark'
import {IconWithToolTip} from 'Components'
// import Rupee from 'SVG/Rupee'

const styles = makeStyles(theme => ({
  image: {
    height: 240,
    backgroundColor: theme.palette.grey[600]
  },
  title: {
    display: "flex",
    width: "100%",
    justifyContent: "space-between",
    alignItems: "center",
    paddingLeft: theme.spacing(1)
  },
  bookmarked: {
    color: theme.palette.secondary.main
  },
  label: {
    padding: theme.spacing(1),
    paddingTop: 0
  },
  priceContainer: {
    display: "flex",
    width: "100%",
    paddingLeft: theme.spacing(1),
    alignItems: "center",
    paddingBottom: theme.spacing(1),
    '& > *': {
      paddingRight: theme.spacing(1)
    }
  },
  slash: {
    textDecoration: "line-through"
  }
}))

function ProductCard(props) {

  const classes = styles()

  return <Grid item xs={6} sm={4} md={3}><Card>
    <CardActionArea onClick={props.productClick}>
      <CardMedia title={props.title} image={props.cached.thumbnail} className={classes.image} />
    </CardActionArea>
    <div className={classes.title}>
      <Typography variant="body1" noWrap={true}>{props.cached.brand}</Typography>
    </div>
    <Typography variant="body2" color="textSecondary" noWrap={true} className={classes.label}>{props.label}</Typography>
    <div className={classes.priceContainer}>
      <Typography display={"inline"}> ₹{Math.ceil(props.price * (100 - props.discount) / 100)}</Typography>
      <Typography display={"inline"} color="textSecondary" variant="caption" className={classes.slash}> ₹{props.price}</Typography>
      <Typography display={"inline"} color="secondary" variant="body2">{props.discount}% OFF</Typography>
    </div>
  </Card>
  </Grid>
}

ProductCard.defaultProps = {
  label: "Yellow t-shirt which has long product name",
  brand: "Nakhre",
  bookmarked: true,
  price: 100,
  discount: 30
}

ProductCard.propTypes = {
  label: PropTypes.string.isRequired,
  images: PropTypes.array.isRequired,
  brand: PropTypes.string.isRequired,
  bookmarked: PropTypes.bool,
  price: PropTypes.number.isRequired,
  discount: PropTypes.number.isRequired,
  productClick: PropTypes.func.isRequired,
  id: PropTypes.string.isRequired
}

export default ProductCard