import React from 'react'
import {Switch, Route} from 'react-router-dom'
import ViewProduct from './View'
import Category from './Add/Category'
import Color from './Add/Color'
import Product from './Add/Product'
import Brand from './Add/Brand'
import SpeedDial from './Add/SpeedDial'

function ProductLayout(props){
  return <div><Switch>
    <Route path={'/product/add/color'} component={Color}/>
    <Route path={'/product/add/category'} component={Category}/>
    <Route path={'/product/add/product'} component={Product} />
    <Route path={'/product/add/brand'} component={Brand} />
    <Route component={ViewProduct} path={'/product/view'}/>
  </Switch>
  <SpeedDial />
  </div>
}

export default ProductLayout