import React from 'react'
import PropTypes from 'prop-types'
import Paper from '@material-ui/core/Paper'
import { useForm, Controller } from "react-hook-form";
import Input from './InputReactHook'
import Typography from '@material-ui/core/Typography'
import Checkbox from './Checkbox'
import Select from './Select'
import MultipleSelect from './MultipleSelect'
import { yupResolver } from '@hookform/resolvers';
import {makeStyles} from '@material-ui/core/styles'
import Upload from './Upload'

const styles = makeStyles(theme => ({
  form: {
    padding: theme.spacing(1),
    '&> *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

function MapItems({item, control}) {
  switch(item.type){
    case 'checkboxGroup':
      return <div>
        <Typography variant="h4">{item.title}</Typography>
        {
          item.items.map((checkbox, index) => <Checkbox key={index} name={item.title+"_"+checkbox} {...checkbox}/>)
        }
      </div>
    case 'checkbox':
      return <Checkbox {...item}/>
    case 'select':
      return <Controller as={<Select {...item}/>} control={control} name={item.name} defaultValue={""}/>
    case 'multipleSelect':
      return <MultipleSelect  {...item}/>
    case 'upload':
      return <Upload {...item}/>
    case "hidden":
      return <input ref={item.inputRef} {...item}/>
    default:
      // console.log(item)
      return <Input {...item}/>
  }
}

function FormGenerator(props) {
  const { register, handleSubmit, errors, control } = useForm({
    resolver: yupResolver(props.validationSchema)
  });
  const classes = styles()
  console.log(errors)
  return <Paper component={"form"} className={classes.form} {...props.formProps} onSubmit={handleSubmit(props.onSubmit)}>
      <Typography variant="h4">{props.title}</Typography>
    {
      props.formSchema.map(item => <MapItems item={{...item, inputRef: register, errors: errors}} key={item.name} control={control}/>)
    }
    {
      props.children
    }
  </Paper>
}

FormGenerator.defaultProps = {
  title: "Add Data",
  validationSchema: {}
}

FormGenerator.propTypes = {
  formSchema: PropTypes.array.isRequired,
  onSubmit: PropTypes.func.isRequired,
  validationSchema: PropTypes.object.isRequired,
  defaultData: PropTypes.object
}

export default FormGenerator