import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import { QueryRenderer } from 'Components'
import OrderRefetch from './OrderRefetch'

const query = graphql`
  query OrderQuery {
    ...OrderRefetch_orders
  }
`

function Order(props) {
  return <div>
    <QueryRenderer query={query} component={data => <OrderRefetch orders={data} />}/>
  </div>
}

export default Order
