import graphql from 'babel-plugin-relay/macro'
import * as yup from 'yup'

export const addSizeForm = [
  {
    type: "text",
    label: "Size",
    helperText: "Try using S, M, L when possible and size in description",
    required: true,
    name: "label"
  },
  {
    type: "text",
    label: "Description",
    helperText: "Add things like shoulder length or height",
    required: true,
    name: "description",
    multiline: true,
    rows: 3
  },
  {
    type: "number",
    label: "Price",
    helperText: "Price of this size. Discount will be applied based on product discount",
    required: true,
    name: "price"
  },
  {
    type: "number",
    label: "Items present",
    helperText: "Number of items present in inventory",
    required: true,
    name: "quantity"
  },
  {
    type: "hidden",
    name: "id",
    value: ""
  }
]

export const addSizeValidation = yup.object().shape({
  price: yup.number().required().min(1).integer(),
  quantity: yup.number().required().integer().min(1),
  description: yup.string().required(),
  label: yup.string().required(),
  id: yup.string().required()
})

export const addSizeMutation = graphql`
mutation AddSizeFormMutation($data: AddSizeInput!) {
  addSize(data: $data) {
    product {
      id
      sizes {
        label
        price
        description
        quantity
      }
    }
  }
}
`