import React from 'react'
import { IconWithToolTip, Modal, FormWithRelay, MaterialTable, LoadingIcon, CommitRelay } from 'Components'
import { useRouteMatch } from 'react-router-dom'
import Add from '@material-ui/icons/Add'
import { addSizeForm, addSizeValidation, addSizeMutation } from './AddSizeForm'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import Delete from '@material-ui/icons/Delete'
import Edit from '@material-ui/icons/Edit'
import deleteSizeMutation from './DeleteSize'
import {snackbarSuccess} from 'Store/actions/snackbar'


function Sizes(props) {

  const [modal, setModal] = React.useState(false)
  const match = useRouteMatch()

  const modifiedForm = addSizeForm.map(item => {
    if(item.name === "id") {
      return ({
        ...item,
        value: match.params.id
      })
    }
    return item
  })

  return <div>
    <Modal open={modal} title={"Add Size"} description={"Add sizes along with their inventory"} 
      hideSubmit={true} handleClose={() => setModal(false)}>
      <FormWithRelay formSchema={modifiedForm} validationSchema={addSizeValidation} mutation={addSizeMutation}>
        <input name="id" type="hidden" value={"123"}/>
      </FormWithRelay>
    </Modal>
    <IndividualSize sizes={props.sizes} id={props.id} onAddClick={() => setModal(true)}/>
  </div>
}

function IndividualSize(props) {

  const [deleteLabel, setDeleteLabel] = React.useState("")
  const [modal, setModal] = React.useState({open: false, data: {}})

  function deleteSize(id, label) {
    if(!window.confirm("Do you wish to delete")) {
      return 
    }
    setDeleteLabel(id)
    CommitRelay({data:{id, label}}, deleteSizeMutation, (response, error) => {
      setDeleteLabel(id)
      if(!error) {
        snackbarSuccess("Deleted size from db")
      }
    })
  }

  function editSize(id, data) {
    console.log(id, data)
  }

  const combined = props.sizes.map(item => ({
    ...item,
    actions: <div style={{display: "flex", flexDirection: "row"}}>
      <LoadingIcon isLoading={item.label === deleteLabel} title={"Delete Size"} icon={<Delete/>} onClick={() => deleteSize(props.id, item.label)}/>
      <LoadingIcon isLoading={item.label === deleteLabel} title={"Edit Size"} icon={<Edit />} onClick={() => editSize(props.id, item)}/>
    </div>
  }))
  return <div>
      <MaterialTable 
        title={"Sizes"}
        addClick={props.onAddClick}
        columns={[
          {id: "label", label: "Name"},
          {id: "description", label: "Description"},
          {id: "price", label: "Price"},
          {id: "quantity", label: "Quantity"},
          {id: "actions", label: "Actions"}
        ]}
        tableData={combined} defaultOrder={"asc"} defaultOrderBy={"label"}/>
    </div>
}

export default createFragmentContainer(Sizes, {
  sizes: graphql`
    fragment Sizes_sizes on ProductSize @relay(plural: true){
      label
      price
      quantity
      description
    }
  `
})