import React from 'react'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import { Typography, Grid } from '@material-ui/core'
import Sizes from './Comps/Sizes'
import {CommitRelay, Corousal, IconWithToolTip, LoadingButton, Modal} from 'Components'
import Form from 'Components/FormComps'
import Delete from '@material-ui/icons/Delete'
import Filter from '@material-ui/icons/Filter'
import { snackbarSuccess, snackbarInfo } from 'Store/actions/snackbar'
import {formSchema, addImageMutation, validationSchema} from './Requests/addImages'

const deleteMutation = graphql`
  mutation ViewProductMutation($id: ID!){
    deleteProduct(id: $id) {
      product {
        id
      }
    }
  }
`

function ViewProduct({product, ...props}) {

  const [modal, setModal] = React.useState(false)
  const [requesting, setRequesting] = React.useState(false)

  function deleteProduct(id) {
    if(!window.confirm("Do you wish to delete?")) {
      return 
    }
    CommitRelay({id}, deleteMutation, (response, error) => {
      if(!error) {
        snackbarSuccess("Product deleted successfully")
      }
    })
  }

  function addImage(data) {
    let poster = data.poster
    if(!poster){
      return snackbarInfo("Select a poster image of product")
    }else if(poster[0].name.search(/(\.jpg|\.jpeg|\.png|\.gif|\.webp)$/i) === -1) {
      return snackbarInfo("Only jpg, png and jpeg images are allowed")
    }else if(poster[0].size > (1024  * 100)) {
      return snackbarInfo("Upload limit of image file is 100KB. Compress and upload image")
    }
    delete data.poster
    data.id = product.id
    setRequesting(true)
    CommitRelay({id: product.id}, addImageMutation, (response, error) => {
      setRequesting(false)
      if(!error) {
        snackbarSuccess("Product uploaded successfully")
      }
    }, {uploadables: {poster: poster[0]}})
  }

  return <Grid container>
    {/* Corousal of images will come here */}
    <Grid item md={5} xs={12}>
      <Corousal slides={product.images}/>
    </Grid>
    <Grid item md={7} xs={12}>
      <IconWithToolTip title={"Delete Product"} icon={<Delete />} onClick={() => deleteProduct(product.id)}/>
      <IconWithToolTip title={"Add more images"} icon={<Filter />} onClick={() => setModal(true)} />
      <Modal open={modal} handleClose={() => setModal(false)} hideSubmit={true}>
        <Form formSchema={formSchema} title={"Add product images"} validationSchema={validationSchema} onSubmit={addImage}>
          <LoadingButton type="submit" isLoading={requesting}>Submit</LoadingButton>
        </Form>
      </Modal>
      <Typography variant="h4" gutterBottom>{product.label}</Typography>
      <Typography variant="body2">{product.description}</Typography>
      <Sizes sizes={product.sizes} id={product.id}/>
    </Grid>
  </Grid>
}

export default createFragmentContainer(ViewProduct, {
  product: graphql`
    fragment ViewProduct_product on Product {
      label
      images
      id
      description
      price
      discount
      sizes {
        ...Sizes_sizes
      }
    }
  `
})