import React from 'react'
import { FormControl, InputLabel, Select, MenuItem, FormHelperText } from '@material-ui/core'
import PropTypes from 'prop-types'

//option [{label, value}]

function CustomSelect(props) {
  // let [value, setValue] = React.useState(props.defaultValue|| "")
  // console.log(props.inputRef)
  // console.log(props.errors)
  return (
    <FormControl fullWidth>
      <InputLabel error={Boolean(props.errors[props.name])}>{props.label}</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          name={props.name}
          fullWidth
          onChange={e => props.onChange(e.target.value)}
          value={props.value}
          onBlur={props.onBlur}
        >
          {
            props.options.map(option => {
              if(option.value !== undefined){
                return <MenuItem key={option.value} value={option.value}>{option.label}</MenuItem>
              } else {
                return <MenuItem key={option} value={option}>{option}</MenuItem>
              }
            })
          }
        </Select>
      <FormHelperText error={Boolean(props.errors[props.name])}>{props.errors[props.name]?.message || props.helperText}</FormHelperText>
    </FormControl>
  )
}

CustomSelect.propTypes = {
  options: PropTypes.array.isRequired,
  name: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  helperText: PropTypes.string
}

export default CustomSelect