import * as yup from 'yup'
import graphql from 'babel-plugin-relay/macro'

export const colorFormSchema = [
  {
    type: "text",
    name: "label",
    required: true,
    label: "Color Name"
  },
  {
    type: "text",
    label: "Color code",
    required: true,
    name: "colorCode"
  },
]

export const colorValidationSchema = yup.object().shape({
  label: yup.string().required().max(20),
  colorCode: yup.string().required().max(6).min(6)
})

export const colorMutation = graphql`
mutation AddColorFormMutation($data: AddColorInput!){
  addColor(data: $data) {
    color {
      label
      colorCode
      id
    }
  }
}
`

export const colorQuery = graphql`
query AddColorFormQuery{
  getColor {
    color {
      label
      colorCode
      id
    }
  }
}
`