import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {CardMedia, MobileStepper} from '@material-ui/core';
import SwipeableViews from 'react-swipeable-views';
const useStyles = makeStyles((theme) => ({
  
  stepper: {
    justifyContent:"center",
    alignItems: "center",
    width: "100%",
    backgroundColor: "transparent"
  },
  root: {
    position: "relative",
    width: "100%"
  }
}));

function Corousal(props) {
  const classes = useStyles()
  let [index, setIndex] = React.useState(0)
  console.log(props.slides[0])
  return <div className={classes.root}>
    <SwipeableViews onChangeIndex={i => setIndex(i)}>
      {
        props.slides?.map(item => <CardMedia key={item} image={item} style={{height: props.height}}/>)
      }
    </SwipeableViews>
    <MobileStepper
      steps={props.slides?.length}
      position="static"
      variant="dots"
      activeStep={index} 
      className={classes.stepper}
      />
  </div>
}

Corousal.defaultProps = {
  height: 480
}

export default Corousal
