import React from 'react'
import { createContainer } from "unstated-next"
import {withRouter} from 'react-router-dom'
import HamburgerMenu from '@material-ui/icons/Menu'
import ThemeSwitch from '@material-ui/icons/BrightnessMedium'
import { makeStyles } from '@material-ui/core/styles'
import IconButton from '@material-ui/core/IconButton'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import {environment, switchTheme} from 'Store/actions/independent'
import {connect} from 'react-redux'
import Explore from '@material-ui/icons/Explore'
import PopUp from 'Components/PopUp'

const drawerWidth = 240;

const styles  = makeStyles(theme => ({
  appBar: {
    backgroundColor: theme.palette.background.paper,
    color: theme.name === "LIGHT" ? "#212121" : "#fff",
    [theme.breakpoints.up('md')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(1),
    [theme.breakpoints.up('md')]: {
      display: 'none',
    }
  },
  headerText: {
    flex: 1
  },
  production: {
    backgroundColor: theme.palette.primary.main,
  },
  local: {
    backgroundColor: theme.palette.background.paper
  }
}))



function Header(props){
  const classes = styles()
  let drawer = DrawerState.useContainer();
  function iconClick() {
    drawer.setDrawer(true)
  }

  function setValue(value, name){
    if(name === "environment") {
      environment(value)
    }else if(name === "theme") {
      switchTheme(value)
    }
  }

  return (
    <AppBar position="fixed" className={classes.appBar}>
      <Toolbar>
        <IconButton onClick={iconClick} className={classes.menuButton}>
          <HamburgerMenu />
        </IconButton>
        <Typography variant="h4" noWrap className={classes.headerText} >
          {props.environment}
        </Typography>
        <PopUp name="environment" options={["Local", "Production", "Testing"]} getValue={setValue} icon={<Explore />} title={"Switch Evironment"}/>
        <PopUp name="theme" options={["ONE_DARK", "LIGHT", "UNICORN"]} getValue={setValue} icon={<ThemeSwitch />} title={"Switch theme"}/>
      </Toolbar>
    </AppBar>
  )
}

const mapStateToProps = state => ({
  environment: state.Independent.environment,
  theme: state.Independent.theme
})

export default connect(mapStateToProps)(withRouter(Header))

function useDrawer() {
  let [drawerOpen, setDrawerOpen] = React.useState(false)
  let setDrawer = (open) => setDrawerOpen(open)
  return {drawerOpen, setDrawer}
}

export const DrawerState = createContainer(useDrawer)