import * as yup from 'yup'
import graphql from 'babel-plugin-relay/macro'

export const categoryFormSchema = [
  {
    type: "text",
    name: "label",
    required: true,
    label: "Category Name"
  },
  {
    type: "select",
    options: ["MAIN", "CHILD", "INTERMEDIATE"],
    required: true,
    label: "Type",
    name: "type"
  },
  {
    type: "upload",
    required: true,
    label: "Category image",
    name: "poster"
  }
]

export const categoryValidationSchema = yup.object().shape({
  label: yup.string().required().max(20),
  type: yup.string().required()
})

export const categoryMutation = graphql`
mutation AddCategoryFormMutation($data: AddCategoryInput!){
  addCategory(data: $data) {
    category {
      label
      id
      parent
      type
      children
    }
    message
  }
}
`

export const categoryQuery = graphql`
query AddCategoryFormQuery{
  getCategory{
    category {
      ...Category_category
    }
  }
}
`